import React, { Component } from "react";
import { withRouter } from "react-router";

class ShowImageScreen extends Component {
    componentDidMount() {
        this.viewPortAdder()
    }
    viewPortAdder() {
        var iefix = document.createElement('meta');
        iefix.setAttribute("name", "viewport");
        iefix.setAttribute("content", "width=device-width, initial-scale=1.0");
        document.getElementsByTagName('head')[0].appendChild(iefix);
    }
    render() {
        return (
            <div>
                <style>{`body{
            width: 100vw !important;
            height: 100vh !important;
            background-color:black !important;
        }`}</style>
                <div style={{ display: "flex", width: "100vw", height: "100vh" }}>
                    <img role="button" src={decodeURIComponent(this.props.match.params.id)} style={{
                        width: "2000px",
                        height: "2000px",
                        maxWidth: "80vw",
                        maxHeight: "80vh",
                        objectFit: "contain",
                        margin: "auto"
                    }} height="auto" />
                </div>
            </div>
        )
    }
}
export default withRouter(ShowImageScreen)